.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin: 0;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: block;
}

.product__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  margin-bottom: 16px;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product__price-cost {
  color: #060b35;
  font-size: 16px;
  line-height: 18px;
}

.product__buy-button {
  justify-content: center;
  display: flex;
}

.product__buy-button-active {
  color: #fff;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 38px;
  font-size: 15px;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.product__buy-button-active:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.product:hover {
  transform: scale(1.2);
}

.product:hover .product__title {
  color: #34568b;
}

.product__stars-reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product__stars-reviews .stars {
  gap: 4px;
  display: flex;
}

.product__stars-reviews .stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.product__stars-reviews .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product__reviews {
  color: #060b35;
  font-size: 10px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__header-text {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav .is-active, .nav__header-text:hover {
  color: #00acdc;
}

.nav__header-text:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 0%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav .is-active:after {
  width: 100%;
}

* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

body, p, h2 {
  margin: 0;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

